*/

@Component({
    components: {
        ModalVue
    },
})
export default class ImageVue extends Vue {

    @Prop() private image!: string|string[];

    private vvconfig = {
        "inline": true,
        "button": false,
        "navbar": false,
        "title": false,
        "toolbar": false,
        "tooltip": false,
        "movable": true,
        "zoomable": true,
        "rotatable": true,
        "scalable": true,
        "transition": true,
        "fullscreen": false,
        "keyboard": false,
        "url": "data-source"
    };


    get images(){
        return Array.isArray(this.image) ? this.image : [this.image];
    }

    zoomInc(){
        this.viewer.zoom(0.1);
    }
    zoomDec(){
        this.viewer.zoom(-0.1);
    }
    rotateL(){
        this.viewer.rotate(-45);
        //this.viewer.moveTo(0,0);
    }
    rotateR(){
        //console.log(this.viewer.imageData.left, this.viewer.imageData.top);
        this.viewer.rotate(45);
    }

    get viewer(){
        const elem = this.$el.querySelector('.v-viewer');
        // @ts-ignore
        return elem.$viewer;
    }


    close(){
        this.$emit('close');
    }


}

